html {
  /* background-color: black; */
  background-image: url(/static/media/home.df441946.jpg);

  /* Full height */
  height: 95%;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Nova Flat', cursive;
}
.playScreen {
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;

}

.heroesList {
  width: 200px;
  height: 480px;
  border: 5px solid white;
  border-radius: 10px;
  color: white;
}

.heroesList li {
  list-style-type: none;
  padding-top: 10px;
}

.player {
  margin-top: 5px;
  margin-left: 5px;
  z-index: 3;
}

.enemy {
  margin-top: 5px;
  margin-left: 5px;
  z-index: 2;
}

.loot {
  margin-top: 5px;
  margin-left: 5px;
  z-index: 1;
}

h1 {
  color: lightgreen;
  font-size: 1.5rem;
  margin: 2rem;
}

.playersId {
  color: lightgreen;
}

.HomeScreen {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  width: 1200px;
  height: 600px;
  color: white;
  margin: 0 auto;
  border: 1 solid white;
  background-clip: green
}

.HomeScreen li {
  list-style-type: none;
  padding-top: 10px;
}

.homeId {
  color: lightgreen;
}

.homeName {
  color: white;
}

.homeStartText {
  height: 0px;
  justify-items: center;
  margin: 100px auto;
  text-align: center;
  color: grey;
  font-size: 3rem;
}

.homeList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0 auto;
}

.homePlayers {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.home-levels {
  height: 800px;
  justify-items: center;
  margin: 0px auto;
  text-align: center;
  color: white;
  font-size: 2rem;
}


.gameTitle {
  font-size: 5rem;
  text-align: center;
}

.homeButton {
  width: 200px;
  height: 100px;
  background-color: dimgray;
  color: white;
}

.homeButtonDiv {
  margin: 0 auto;
  padding-top: 50px;
}

.information {
  text-align: center;
}

.information2 {
  text-align: center;
  padding-top: 100px;
  color: rgb(127, 179, 127);
  font-size: 0.8rem;
}

.gameHeader {
  margin: 0 auto;
  width: 90vw;
  height: 60px;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.loot-message {
  font-size: 3rem;
  font-family: 'Nova Flat', cursive;
  background-color: rgba(0, 80, 255, 0.7);
  top: 0;
  left: 0;
  width: 810px;
  height: 490px;
  z-index: 9;
  position: absolute;
  text-align: center;
}

.loot-message-title {
  color: lightgreen;
  margin-top: 100px;
}

.loot-message-text {
  font-size: 2rem;
  text-decoration: none;
}

.loot-message-text a {
  color: white;
}

a {
  text-decoration: none;
}

a:visited {
  color: white;
}

.buttonsDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.buttonsDiv img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.buttonsDiv img:focus {
  outline: none;
}

.controlButtons {
  width: 40px;
  height: 40px;
  padding-left: 3px;
}
.tile {
  display: -webkit-inline-flex;
  display: inline-flex;
  background-color: rgb(79, 170, 105);
  background-image: url('/tiles/base.png')

}

.rock {
  background-image: url('/tiles/rock.png')
}

.tree {
  background-image: url('/tiles/tree.png')
}

.chest {
  background-image: url('/tiles/chest.png')
}

.enemy {
  background-image: url('/tiles/enemy.png')
}

.portal {
  background-image: url('/tiles/portal.png')
}
